export function validateFirstname(rule,value,callback){
    if(value ===''){
        callback(new Error('Please input firstname'))
    }else{
        callback();
    }
}

export function validateLastname(rule,value,callback){
    if(value ===''){
        callback(new Error('Please input lastname'))
    }else{
        callback();
    }
}

export function validateEmail(rule,value, callback) {
  let re = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
  if (value === '') {
    callback(new Error('Please input email'));
  } else if (re.test(value)) {
    callback();
  } else {
    callback(new Error('Invalid email address'));
  }
}

export function validatePhoneNumber(rule,phoneNumber,callback) {
    
    // 04xx xxx xxx+614xx xxx xxxor 00614xx xxx xxx
   
    const pattern = /^4\d{8}$/
    
    if (pattern.test(phoneNumber)) {
      callback(new Error('Invalid phone number'))
    } else {
      callback()
    }
  }