            <el-checkbox-group v-model="checkboxGroup1" >              
                <!-- <el-checkbox v-for="s in services1" :label="s.name" :key="s.id" style="display:block" >{{s.name}}--{{s.time}}h--${{s.price}} <el-icon-info></el-icon-info></el-checkbox> -->
