                    <el-checkbox :label="s.name">{{s.name}}--{{s.time}}h--${{s.price}}</el-checkbox> 
                    <el-button type="info" icon="el-icon-chat-dot-square" circle size="mini" @click="openDialog(s)"></el-button>
            </div>
        </el-checkbox-group>
       

        <el-dialog  :visible.sync="dialogVisible" :title="currentService.name" append-to-body customClass="customWidth">
