            <span>7-SEATER, VAN & LIGHT TRUCK</span>
            <el-checkbox-group v-model="checkboxGroup3" >              
                <!-- <el-checkbox v-for="s in services1" :label="s.name" :key="s.id" style="display:block" >{{s.name}}--{{s.time}}h--${{s.price}} <el-icon-info></el-icon-info></el-checkbox> -->
                <div v-for="s in services3" :key="s.id" class="checkbox-container">
                         
                    <!-- <i class="el-icon-eleme" @click="openDialog(s)">info</i> -->
                    
                    <el-checkbox :label="s.name">{{s.name}}--{{s.time}}h--${{s.price}}</el-checkbox> 
                    <el-button type="info" icon="el-icon-chat-dot-square" circle size="mini" @click="openDialog(s)"></el-button>
            </div>
        </el-checkbox-group>
       

        <el-dialog  :visible.sync="dialogVisible" :title="currentService.name" append-to-body customClass="customWidth">
            <ul id="ul">
            <li v-for="d in description" :key="d" id="li">{{ d }}</li>
          </ul>
