<!--  -->
<template>
<div>
    <HeadNav></HeadNav>
    <el-page-header id="header" @back="goBack" content="Please enter your details">
        </el-page-header>
        <div  class="steps" >
            <el-steps :active="3">
                <el-step title="Step 1" ></el-step>
                <el-step title="Step 2" ></el-step>
                <el-step title="Step 3" ></el-step>
             </el-steps>
        </div>
    <el-card>
       
    <div>
        <el-form ref="form" :model="form" :rules="rules" hide-required-asterisk >
            <el-form-item label="First Name" prop="firstName">
                <el-input v-model="form.firstName"></el-input>
            </el-form-item>
            <el-form-item label="Last Name" prop="lastName">
                <el-input v-model="form.lastName"></el-input>
            </el-form-item>
            <el-form-item label="Mobile Number" prop="phoneNumber">
                <el-input v-model="form.phoneNumber">
                    <template slot="prepend">+61</template>
                </el-input>
            </el-form-item>
            <el-form-item label="Email" type="email" prop="email">
                <el-input v-model="form.email"></el-input>
            </el-form-item>
            <el-form-item label="Address1" prop="address1">
                <el-input  v-model="form.address1"></el-input>
            </el-form-item>
            <el-form-item label="Address2(optional)" >
                <el-input  v-model="form.address2"></el-input>
            </el-form-item>
            <el-form-item label="Suburb" prop="suburb">
                <el-input  v-model="form.suburb"></el-input>
            </el-form-item>
            <el-form-item label="State" prop="state">
                <el-input  v-model="form.state"></el-input>
            </el-form-item>
            <el-form-item label="Post Code" prop="postCode">
                <el-input  v-model="form.postCode"></el-input>
            </el-form-item>
            <!-- <el-radio lable="Policy" prop="policy" v-model="form.policy">I agree to Hao's Garage privacy policy </el-radio>-->
            <el-form-item  prop="policy">
            <el-radio-group v-model="form.policy" >
                <el-radio label="true">I agree to Hao's Garage privacy policy </el-radio>
                <el-radio label="false" v-show="false"></el-radio>
            </el-radio-group>

            <i class="el-icon-info" @click="showPolicy"></i>
            </el-form-item>
            
        </el-form>

        <el-dialog  :visible.sync="dialogVisible" title="Our Policy" append-to-body customClass="customWidth">
            <div>
                <span>By making a booking, you acknowledge that you have accurately chosen the appropriate service and time duration for your vehicle's condition. In the event that our team requires more time than initially allotted for your chosen service, an additional fee will be applied.</span><br><br>
           <span class="dialog">Regarding payment, we operate on a cash-on-delivery (COD) basis, similar to other car wash services. We also offer electronic funds transfer at point of sale (EFTPOS) facilities if needed. </span><br><br>
           <span class="dialog">To successfully carry out the detailing service for your vehicle, we require access to water and a power supply. Additionally, you need to provide a secure off-road parking space that complies with legal regulations, and this parking space should be arranged and provided by you, the vehicle owner.</span><br><br>
           <span class="dialog">When it comes to your personal information, Hao's Garage collects such information through customer-provided means, whether verbally or through online booking. Rest assured that no personal customer information will be shared with any external party outside of the Hao's Garage business. The sole purpose of retaining customer details is to ensure the best possible customer service.</span><br><br>
           <span class="dialog">A protocol is in place for employees who are authorized and obligated to utilize body-worn cameras during vehicle detailing procedures. The focus of this procedure is to maintain the privacy of individuals. Personal information, including license plates, captured by camera footage will be treated confidentially by all personnel who have access to it. This handling aligns with government privacy policies and legal requirements.
        </span><br><br>
            </div>
           
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">Close</el-button>
      </span>
    </el-dialog>
    </div>

    <el-button @click="submit('form')" type="primary">confirm</el-button>
</el-card>
    
</div>
</template>

<script>
import {
    validateFirstname,                       // These are used to verify the email address and password respectively
    validateLastname,
    validateEmail,
    // validatePhoneNumber
             
    } from '../../utils/validate';

    import HeadNav from "../HeadNav.vue"
    
export default {
name:"step-three",
components: {
    HeadNav,
  },
data() {
return {
    chosenTime:-1,
    chosenDate:new Date(),
    totalTime:0,
    checkboxGroup1:[],
    checkboxGroup2:[],
    checkboxGroup3:[],
    dialogVisible:false,
    // fileContent:"",
    
   
    form:{
        firstName:"",
         lastName:"",
         phoneNumber:"",
         email:"",
        address1:"",
        address2:"",
        suburb:"",
        state:"",
        postCode:"",
        policy:""
       
    },

    rules: {
        firstName: [
            { required: true, message: 'Please enter your first name', trigger: 'blur' },                 // Pop up when the email address is empty
            { validator: validateFirstname, trigger: 'blur' },
          ],
          lastName: [
            { required: true, message: 'Please enter your first name', trigger: 'blur' },                     // Pop up when the user enter a wrong password
            { validator: validateLastname, trigger: 'blur' },
          ],
          phoneNumber: [
            { required: true, message: 'Please enter your phone number', trigger: 'blur' },                     // Pop up when the user enter a wrong password
            // { validator: validatePhoneNumber, trigger: 'blur' },
          ],
          email: [
            { required: true, message: 'Please enter your email', trigger: 'blur' },                     // Pop up when the user enter a wrong password
            { validator: validateEmail, trigger: 'blur' },
          ],
          address1:[
            {required: true, message: 'Please enter your address!', trigger: 'blur'}
          ],
          suburb:[
            {required: true, message: 'Please enter  suburb!', trigger: 'blur'}
          ],
          state:[
            {required: true, message: 'Please enter state!', trigger: 'blur'}
          ],
          postCode:[
            {required: true, message: 'Please enter your post code!', trigger: 'blur'}
          ],
          policy:[
            {required: true, message: 'Sorry, you must accept the privacy policy before proceeding!', trigger: 'change'}
          ],
          
        },

}
},

methods:{
    submit(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
                    this.$http.post('/submitBooking',{
                    form:this.form,
                    chosenDate:this.chosenDate,
                    chosenTime:this.chosenTime,
                    totalTime:this.totalTime,
                    checkboxGroup1:this.checkboxGroup1,
                    checkboxGroup2:this.checkboxGroup2,
                    checkboxGroup3:this.checkboxGroup3
                }).then(res=>{
                    if(res.data.flag){
                        this.$message.success(res.data.message);
                        this.$router.push("/index")
                    }else{
                        this.$message.error(res.data.message)
                    }       
                })
          } else {
            console.log('error submit!!');
            return false;
          }
        });

        
    },

    showPolicy(){
        this.dialogVisible=true},


    goBack(){
        this.$router.go(-1)
    },
    
    // async loadTextFile() {
    //   try {
    //     const response = await axios.get("../../public/policy"); // Replace with your file path
    //     this.fileContent = response.data;
    //   } catch (error) {
    //     console.error("Error loading text file:", error);
    //   }
    // }

},

created() {
    this.chosenTime=Number(localStorage.getItem('chosenTime'))
    this.chosenDate=new Date(JSON.parse(localStorage.getItem('chosenDate')))
    this.checkboxGroup1=JSON.parse(localStorage.getItem('checkboxGroup1'))
    this.checkboxGroup2=JSON.parse(localStorage.getItem('checkboxGroup2'))
    this.checkboxGroup3=JSON.parse(localStorage.getItem('checkboxGroup3'))
    this.totalTime=Number(localStorage.getItem('totalTime'))

},
// - DOM
mounted() {
    // this.loadTextFile();
    // console.log(this.fileContent)

}
}
</script>
<style scoped>
/* @import url(); css */
#submitBtn{
    font-size: 2rem;
    margin-top: 10px;
}

.steps{
    /* display:flex;
    justify-content:center; */
    margin:auto;
    width: 50%;
    text-align: left;
    padding: 20px;
     
    }

.dialog{
    word-break:keep-all
}

.el-page-header{
    margin-top: 10px;
}

.el-card{
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}

.el-form-item{
    margin-top: 5px;


}

#header{
    display:flex;
    justify-content: center;
    margin-top: 10px;
    margin-left: 0%;
    width: 100%;
}
@media screen and (max-width: 400px)  { 
    .steps{
    /* display:flex;
    justify-content:center; */
    margin:auto;
    width: 80%;
    text-align: left;
    padding: 20px;
     
    }

    .card{
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    }
   }



</style>