<script>
import HeadNav from "./HeadNav"

export default{
    name:"home-page",
    components: {
    HeadNav,
  },
    data:function() {
            return {
                active: 0,
                ShowIndex:0,
                display: 'none',
                MinDisplay:'flex',
                //Vuev-for
                imgs:[
                    {"src":require('../assets/car-interior-detailing-vip.jpeg')},
                    {"src":require('../assets/car-interior-detailing-vip.jpeg')},
                    {"src":require('../assets/car-interior-detailing-vip.jpeg')},
                    {"src":require('../assets/car-interior-detailing-vip.jpeg')},
                    {"src":require('../assets/car-interior-detailing-vip.jpeg')},
                    {"src":require('../assets/car-interior-detailing-vip.jpeg')},
                    {"src":require('../assets/car-interior-detailing-vip.jpeg')},
                    {"src":require('../assets/car-interior-detailing-vip.jpeg')},
                    {"src":require('../assets/car-interior-detailing-vip.jpeg')},
                ],
                navBarFixed: false,
                description:[],
                titleOfDescription:"",
                dialogVisible: false,

                services:["Regular Detailing",
                "Interior Detailing",
                "Exterior Detailing","Full Detailing",
                "Pre-sale Detailing or usedcar buyer's option"]
            };
        },
        mounted() {
            // 
            window.addEventListener('scroll', this.onScroll, false)
        },
         destroy() {
            // vue
            window.removeEventListener('scroll', this.onScroll)
        },

        methods:{
            // async showDetails(s){
            //   this.description=[]
            //   try {
            //     await this.getDescription(s)
            //     this.showDescription(s)
            //   }catch(err){
            //     console.error('error:',err)
            //   }
            // },

            async getDescription(s){
              let response
              await this.$http.post('/getDescription',{title:s})
              .then((res)=>{
                if(res.data.flag){
                  this.description=res.data.description
                  this.titleOfDescription=s
                  response=true
                }else{
                  response=res.data.message
                }
              })
              return response
            },

           showDescription(s){
             this.getDescription(s).then((res)=>{
                if(res==true){
                  this.dialogVisible=true
                }else{
                  this.$message.error(res)
                }
             })
           },

            ZoomIn(i){
               this.display='block';
                this.MinDisplay='none';
                this.ShowIndex=i;
            },
            ZoomOut(){
                this.display='none';
                this.MinDisplay='flex';
            },
            select(i){
                this.ShowIndex=i;
            },
            onScroll() {
                    // 
                    const navContents = document.querySelectorAll('.box-card')
                    // console.log(navContents.length)
                    //  offsetTop
                    const offsetTopArr = []
                    navContents.forEach(item => {
                        offsetTopArr.push(item.offsetTop)
                    })
                    //  scrollTop
                    const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
                    // 
                    let navIndex = 0
                    for (let n = 0; n < offsetTopArr.length; n++) {
                        //  scrollTop n offsetTop  n-1 
                        // n
                        if (scrollTop >= offsetTopArr[n]) {
                        navIndex = n
                        }
                    }
                    this.active = navIndex
            },
            scrollTo(index) {
      //  offsetTop
      // css 1  0  +1
      const elements = document.querySelectorAll('.box-card');
      const targetOffsetTop = elements[index].offsetTop
      //  offsetTop
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      //  50 
      const STEP = 50
      // 
      if (scrollTop > targetOffsetTop) {
        // 
        smoothUp()
      } else {
        // 
        smoothDown()
      }
      // 
      function smoothDown() {
        //  scrollTop  targetOffsetTop 
        if (scrollTop < targetOffsetTop) {
          //  STEP  STEP
          // 
          if (targetOffsetTop - scrollTop >= STEP) {
            scrollTop += STEP
          } else {
            scrollTop = targetOffsetTop
          }
          document.body.scrollTop = scrollTop
          document.documentElement.scrollTop = scrollTop
          //  requestAnimationFrame  setInterval 
          requestAnimationFrame(smoothDown)
        }
      }
      // 
      function smoothUp() {
        if (scrollTop > targetOffsetTop) {
          if (scrollTop - targetOffsetTop >= STEP) {
            scrollTop -= STEP
          } else {
            scrollTop = targetOffsetTop
          }
          document.body.scrollTop = scrollTop
          document.documentElement.scrollTop = scrollTop
          requestAnimationFrame(smoothUp)
        }
      }
    }

        },
       
}
</script>


